* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: GoogleSans-Regular, Arial;
    background-color: var(--main-layout-bg);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

a {
    text-decoration: none;
    color: inherit;
}

img {
    height: 100%;
    width: 100%;
    /* object-fit: cover; */
}
